<template>
  <div id="dashboard">
    <ul class="collection with-header">
        <li class="collection-header">Employees</li>
        <router-link v-bind:to="{name:'view-employee', params: {employee_id: employee.employee_id}}" 
        v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <div class="chip">{{employee.dept}}</div>
                {{employee.employee_id}}: {{employee.name}}
        </router-link>
    </ul>
    <div class="fixed-action-btn">
        <router-link class="btn-floating red" to="/new">
        <i class="fa fa-plus"></i>
        </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";

export default {
  name: "dashboard",
  data() {
    return {
      employees: []
    };
  },
  created() {
    db
      .collection("employees")
      .get()
      .then(query => {
        query.forEach(doc => {
          console.log(doc.data());
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>




